const responses = {
  'auth/weak-password': 'Digite uma senha com pelo menos 8 caracteres',
  'auth/operation-not-allowed': 'Operao no permitida, contate o administrador',
  'auth/email-already-in-use': 'O e-mail digitado j est em uso',
  'auth/invalid-email': 'O e-mail digitado  invlido',
  'auth/user-disabled': 'A conta digitada est desativada pela administrao',
  'auth/email-not-verified': 'O e-mail digitado no est ativo. Siga as instrues enviadas por e-mail para a ativao',
  'auth/user-not-found': 'No foi possvel encontrar a sua conta do CIAPD',
  'auth/wrong-password': 'O e-mail e/ou a senha digitados esto incorretos',
  'auth/too-many-requests': 'Uma atividade incomum foi detectada. Tente novamente em alguns segundos',
  'auth/missing-permissions': 'A combinao digitada no pertence a rea de empresas',
  'firestore/unknown-error': 'Erro ao acessar as informaes de sua conta, contate um administrador',
  'firestore/user-not-found': 'O e-mail e senha digitados no esto cadastrados no Firestore, contate um administrador',
}

export default (type = 'default') => (
  responses.hasOwnProperty(type)
    ? responses[type]
    : 'Servio indisponvel, tente novamente mais tarde'
)
