const responses = {
  'auth/weak-password': 'Digite uma senha com pelo menos 8 caracteres',
  'auth/operation-not-allowed': 'Operao no permitida, contate o administrador',
  'auth/email-already-in-use': 'O e-mail digitado j est em uso',
  'auth/invalid-email': 'O e-mail digitado  invlido',
  'general/invalid-role': 'Cargo invlido, contate um administrador',
  'general/invalid-type': 'Tipo invlido, contate um administrador',
  'auth/user-not-found': 'A conta digitada no existe',
  'firestore/unknown-error': 'Erro ao acessar as informaes de sua conta, contate um administrador',
}

export default (type = 'default') => (
  responses.hasOwnProperty(type)
    ? responses[type]
    : 'Servio indisponvel, tente novamente mais tarde'
)
